export const query = graphql`
  query($pathSlug: String!) {
    markdownRemark(frontmatter: { path: { eq: $pathSlug } }) {
      html
      frontmatter {
        date
        title
        tags
        mmmmLinks{
          id
          link
        }
        features{
          id
          justifyCont
          background
          features{
            id
            feature
          }
          featureHeader
          featureHeadCol
          featureCol
          borderCol
          videoPath
        }
        ffddg
        getST
        getSTSesc
        download
        docs
        docsDesc
        resourss
        
        resourcesArr{
        id
        resourceName
        resourceLink
        haveList
        resDescList{
          id
          desc
        }
        }
        downImg{
          childImageSharp {            
            fluid(              
              quality: 50 
              maxWidth: 200                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        ResImg {
          childImageSharp {            
            fluid(              
              quality: 50 
              maxWidth: 200                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        DocsImg {
          childImageSharp {            
            fluid(              
              quality: 50 
              maxWidth: 200                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        main_text
        main_image {
          childImageSharp {
            
            fluid(              
              quality: 80 
              maxWidth: 800                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        cover {
          childImageSharp {
            fluid(              
              quality: 10     
              maxWidth: 900         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        youtube_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        itch_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        unitystore_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        docs_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 200         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }

      }
    }
  }
`;